.hero {
    height: 100vh;
    display: flex;
    justify-content: space-between;
}

.tl {
    font-size: 8.68rem;
}

.name {
    font-size: 3rem;
}

.hero_text {
    text-align: right;
    padding-right: 5%;
    height: fit-content;
    transform: translateY(36vh);
}

#logo path:nth-child(1) {
    stroke-dasharray: 781px;
    stroke-dashoffset: 781px;
    animation: line-anim 2s ease forwards;
}

